import Vue from 'vue'
import VueRouter from 'vue-router'
import VueResource from 'vue-resource'
import VueJsonp from 'vue-jsonp'
import home from 'components/home/home.vue'
import  shoping from 'components/shoping/shoping.vue'
import cart from 'components/cart/cart.vue'
import personalCenter from 'components/personalCenter/personalCenter.vue'
import Index from './login.vue'
import 'common/stylus/index.styl'
import   "common/css/reset.css"
import   "common/css/animate.css"

//使用路由
Vue.use(VueRouter)
Vue.use(VueResource)
Vue.use(VueJsonp)
const routes = [
  {path: '*', component: home},
  {path: '/main/home', component: home},
  {path: '/main/shoping', component: shoping},
  {path: '/main/cart', component: cart},
  {path: '/main/center', component: personalCenter},
]
// 创建路由
const router = new VueRouter({
  routes
});
// 4. 创建和挂载根实例。
// 记得要通过 router 配置参数注入路由，
// 从而让整个应用都有路由功能
new Vue({
  template: '<Index/>',
  components: {Index},
  router,
  data: {
    eventHub: new Vue() // 用于子控件向父控件传递事件  radio需要
  }
}).$mount('#app')



